.profile_container {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Alinha os itens ao topo */
    width: 100%;
    height: 100vh; /* Ajusta a altura para ocupar a tela inteira */
    flex-direction: row;
}

.profile_box_photo {
    display: flex;
    flex-direction: column;
    width: 20vw;
    height: 55vh;
    border: 1px solid rgb(184, 184, 184);
    /* box-shadow:rgba(0, 0, 0, 0.309) 0px 0px 10px 1px; */
    border-radius: 30px;
    background-color: rgb(255, 255, 255);
    margin-top: 10vh;
    padding: 40px 10px 0px 0px;
    align-items: center;
}

.profile_main {
    display: flex;
    width: 20w;
    flex-direction: column;
    align-items: center;
    padding: 0px 50px 0px 15%;  
}

.profile_photo {
    display: flex;
    background-color: #D9D9D9;
    border-radius: 80px;
    height: 170px;
    width: 170px;
}

.profile_name {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 20px;
}

.profile_name_user {
    font-size: 20px;
    font-weight: bold;
}

.profile_name_username {
    font-size: 15px;
    color: #838181;
}

.profile_information_text {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.profile_information_date,
.profile_information_Location,
.profile_information_lenguages {
    font-size: 15px;
    color: #7a7a7a;
    margin-bottom: 10px;
}

.profile_button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 53%;
    height: 6vh;
    background-color: #D9D9D9;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    margin-top: 3vh;
    border-radius: 10px;
    color: rgb(0, 0, 0);
    text-decoration: none;
}

.profile_button_explorer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 97%;
    font-size: 17px;
    height: 6vh;
    background-color: #D9D9D9;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    margin-top: 5vh;
    border-radius: 10px;
    color: rgb(0, 0, 0);
    text-decoration: none;
}
.profile_right_column {
    display: flex;
    width: 55vw; 
    padding-left: 10%;
    flex-direction: column;
}
.profile_right_column_text_title{
    display: flex;
    width: 70%;
    margin-top: 9vh;
    font-size: 17px;
    
}
.profile_rigth_column_box_one{
    display: flex;
    width: 70%;
    height: 10vh;
    flex-direction: column;
    margin-top: 2vh;
    margin-bottom: 1vh;
    border-radius: 20px;
    border: 1px solid rgb(184, 184, 184);
}
.profile_rigth_column_box_one_title{
    display: flex;
    font-weight: bold;
    font-size: 17px;
    padding: 25px 0px 0px 20px;
}
.profile_rigth_column_box_one_text{
    display: flex;
    padding: 0px 0px 0px 20px;
}
.profile_right_column_evaluation{
    display: flex;
    width: 70%;
    height:25vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 40px;
    border: 1px solid rgb(184, 184, 184);
    margin-top: 40px;
}
.profile_rigth_column_evaluation_title{
    display: flex;
    font-size: 25px;
    padding: 0px 0px 30px 0px;
    font-weight: bold;
}
.profile_right_column_evaluation_text{
    display: flex;
    padding: 30px 0px 0px 0px;

}
.profile_link_freelancer{
    display: flex;
    padding: 30px 0px 0px 0px;
    width: 370px;
    font-size: 15px;
    cursor: pointer;
    line-break: loose;
}
.profile_link_freelancer:hover{
    color:#0096D1 ;


}